      })
      .then(function(myJson) {
        self.jobs = myJson.filter((item)=>item.jobName.match(key))
        self.currPage = 1;
      });
      } else if (self.jobcategory === "projectmanager"){
        var key = 'project manager|Project Manager|pm|專案';
      fetch("https://wetogetherjobdb.herokuapp.com/job?q=project+manager")
      .then(function(response) {
        return response.json();
      })
      .then(function(myJson) {
        self.jobs = myJson.filter((item)=>item.jobName.match(key))
        self.currPage = 1;
      });
      } else {
        console.log(value[value.length - 1])
      }
    },
    open() {
      this.$alert('这是一段内容', '标题名称', {
        confirmButtonText: '确定',
        callback: action => {
          this.$message({
            type: 'info',
            message: `action: ${ action }`
          });
        }
      });
    },
    setPage: function(idx){
      if (idx <= 0 || idx > this.totalPage) {
        return;
      } else {
        this.currPage = idx;
      }
    }
  },
  computed: {
    filteredRows: function(){
      var filter_name = this.filter_name.toLowerCase();
      return (this.filter_name.trim()!=='')?
        this.jobs.filter(function(d){return d.simpleJD.toLowerCase().indexOf(filter_name) > -1;}):
        this.jobs;
    },
    pageStart: function(){
      return (this.currPage - 1)* this.countOfPage;
    },
    totalPage: function(){
      return Math.ceil(this.filteredRows.length / this.countOfPage);
    },
    id(){
      return category[this.$route.params.id];
    }
  },
}


</script>

